@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");
:root {
  --font-family: "Open Sans", sans-serif;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */
* {
  font-family: "Roboto", sans-serif;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.widget {
  margin-top: 30px;
}
.widget__title {
  position: relative;
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.16;
  color: #000000;
  cursor: pointer;
}
.widget__title::after {
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  right: 0;
  top: 0;
  background-image: url(./../../img/icons/chevron-up.svg);
  background-repeat: no-repeat;
  background-size: initial;
  background-position: center;
}
.widget__title--active::after {
  transform: rotate(180deg);
}
.widget__body--hidden {
  display: none;
}
.widget__hidden {
  display: none;
}
.widget__show-hidden {
  display: inline-block;
  margin-top: 15px;
  padding: 0;
  background: transparent;
  border: none;
  font-size: 16px;
  line-height: 1.19;
  color: #4290c9;
  cursor: pointer;
  transition: 0.2s ease-in;
}
.widget__show-hidden:hover {
  color: #40525f;
  text-decoration: underline;
}

.cards-holder {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.cards-holder > .card-link {
  margin-bottom: 30px;
}

.card-link--hidden {
  display: none !important;
}

.card-link {
  display: inline-block;
  transition: all 0.2s ease-in;
}
.card-link:hover {
  opacity: 0.8;
  transform: translateY(-5px);
}

.card {
  position: relative;
  width: 270px;
  background-color: #fff;
}
.card__badges {
  position: absolute;
  top: 10px;
  left: 15px;
  right: 15px;
  display: flex;
}
.card__badges > * + * {
  margin-left: 10px;
}
.card__badge {
  display: inline-block;
  height: 22px;
  padding: 0 10px;
  background-color: #525252;
  border-radius: 4px;
  font-size: 14px;
  line-height: 23px;
  color: #000000;
}
.card__badge--class {
  background-color: #ffcf26;
}
.card__badge--credit {
  background-color: #8a7a42;
}
.card__content {
  padding: 16px 15px 20px;
}
.card__title {
  margin: 0;
  margin-bottom: 11px;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.16;
  color: #000000;
}
.card__term {
  margin-bottom: 11px;
  font-size: 14px;
  line-height: 1.14;
  color: #000000;
}
.card__metro {
  margin-bottom: 11px;
  font-size: 14px;
  line-height: 1.14;
  color: #000000;
}
.card__metro::before {
  content: "";
  margin-right: 10px;
  display: inline-block;
  vertical-align: baseline;
  width: 16px;
  height: 11px;
  background: url("./../../img/icons/metro.svg") no-repeat;
  background-size: initial;
}
.card__addres {
  font-size: 14px;
  line-height: 1.14;
  color: #777777;
}

.btn-more {
  width: 100%;
  height: 80px;
  background-color: #e5e5e5;
  border-radius: 4px;
  border: none;
  text-align: center;
  color: #4290c9;
  cursor: pointer;
  transform: background-color 0.2s ease-in;
}
.btn-more:hover {
  background-color: #cccccc;
}
.btn-more img {
  margin-right: 10px;
  vertical-align: bottom;
}

.form-btn {
  width: 100%;
  height: 50px;
  border: 1px solid #d6d6d6;
  border-radius: 4px;
  background-color: transparent;
  text-align: center;
  font-size: 16px;
  line-height: 1;
  color: #3c3c3c;
  cursor: pointer;
  transition: background-color 0.2s ease-in;
}
.form-btn img {
  display: inline-block;
  margin-right: -2px;
  vertical-align: middle;
}
.form-btn + .form-btn {
  margin-top: 10px;
}
.form-btn:hover {
  background-color: rgb(223, 223, 223);
}

.form-btn--submit {
  background: #ffcf26;
  border: none;
  color: #000;
}
.form-btn--submit:hover {
  background: #d6a800;
}

.location__row {
  display: flex;
}
.location__row:first-child {
  overflow: hidden;
  border-radius: 4px 4px 0 0;
  border: 1px solid #d6d6d6;
}
.location__row:last-child {
  overflow: hidden;
  border-radius: 0 0 4px 4px;
  border: 1px solid #d6d6d6;
  border-top: none;
}
.location__btn {
  flex-grow: 1;
  height: 50px;
  padding: 0 15px;
  background-color: transparent;
  border: none;
  text-align: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 50px;
  color: #3c3c3c;
  cursor: pointer;
  transition: background-color 0.3s ease-in;
}
.location__checkbox {
  width: 1px;
  height: 1px;
  position: absolute;
  opacity: 0;
  appearance: none;
}
.location__checkbox:checked + .location__btn {
  background-color: #ffcf26;
}
.location__checkbox:checked + .location__btn:hover {
  background-color: #e0b000;
}

.radio {
  display: block;
  cursor: pointer;
}
.radio {
  margin-top: 15px;
}
.radio__real {
  width: 1px;
  height: 1px;
  position: absolute;
  opacity: 0;
  appearance: none;
}
.radio__fake {
  position: relative;
  display: inline-block;
  margin-right: 10px;
  width: 20px;
  height: 20px;
  background-color: #fff;
  border: 1px solid #777777;
  border-radius: 50%;
  vertical-align: bottom;
}
.radio__real:checked + .radio__fake {
  background-color: #ffe074;
}
.radio__real:checked + .radio__fake::before {
  position: absolute;
  top: 50%;
  left: 50%;
  content: "";
  border-radius: 50%;
  width: 8px;
  height: 8px;
  background: #3c3c3c;
  transform: translate(-50%, -50%);
}
.radio__real:checked ~ .radio__title {
  font-weight: 700;
}
.radio__title {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.18;
  color: #000000;
}

.checkbox {
  display: block;
  cursor: pointer;
}
.checkbox {
  margin-top: 15px;
}
.checkbox__real {
  width: 1px;
  height: 1px;
  position: absolute;
  opacity: 0;
  appearance: none;
}
.checkbox__fake {
  position: relative;
  display: inline-block;
  margin-right: 10px;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border: 1px solid #777777;
  border-radius: 2px;
  vertical-align: bottom;
}
.checkbox__real:checked + .checkbox__fake {
  background-color: #ffe074;
}
.checkbox__real:checked + .checkbox__fake::before {
  position: absolute;
  top: 50%;
  left: 50%;
  content: "";
  width: 18px;
  height: 18px;
  background-image: url(./../../img/icons/check.svg);
  background-size: initial;
  background-position: center;
  transform: translate(-50%, -50%);
}
.checkbox__title {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.18;
  color: #000000;
}
.checkbox__real:checked ~ .checkbox__title {
  font-weight: 700;
}

.toggle {
  cursor: pointer;
}
.toggle__real {
  width: 1px;
  height: 1px;
  position: absolute;
  opacity: 0;
  appearance: none;
}
.toggle__fake {
  position: relative;
  display: inline-block;
  margin-right: 10px;
  width: 37px;
  height: 20px;
  background: #c4c4c4;
  border-radius: 10px;
  vertical-align: bottom;
}
.toggle__fake::before {
  position: absolute;
  top: 2px;
  left: 2px;
  content: "";
  width: 16px;
  height: 16px;
  background: #3c3c3c;
  border-radius: 50%;
  transition: all 0.2s ease-in;
}
.toggle__real:checked + .toggle__fake {
  background: #ffe074;
}
.toggle__real:checked + .toggle__fake::before {
  left: 19px;
}
.toggle__title {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.18;
  color: #000000;
}
.toggle__real:checked ~ .toggle__title {
  font-weight: 700;
}

.toggle-sidebar {
  position: absolute;
  right: 15px;
  top: 15px;
  width: 30px;
  height: 8px;
  background-color: black;
  border: none;
}

@media (max-width: 1200px) {
  .container {
    max-width: 830px;
  }
}
@media (max-width: 829px) {
  .container {
    max-width: 600px;
  }
  .sidebar {
    margin-left: 0 !important;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    background-color: aliceblue;
    padding: 15px;
    box-shadow: 0px 0px 20px 4px rgba(0, 0, 0, 0.37);
    overflow-y: scroll;
    transform: translateX(-150%);
    transition: translateX 0.2s ease-in;
  }
  .sidebar--mobile-active {
    transform: translateX(0%);
  }
}
@media (max-width: 599px) {
  .cards-holder {
    flex-direction: column;
    align-items: center;
  }
}
body {
  background-color: #f4f4f4;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 15px;
}

.main {
  width: 870px;
}

.sidebar {
  min-width: 200px;
  width: 270px;
}

.main + .sidebar {
  margin-left: 30px;
}

.row {
  display: flex;
  justify-content: space-between;
}